.about {
    //min-width: 100%;
    padding: 20rem 12rem;

    @media(max-width: 1064px) {
        padding: 14rem 8rem;
    }

    @media(max-width: 600px) {
        padding: 8rem 2rem;
    }

    &__text {
        font-family: 'Ekstra'; 
        font-size: 2.8rem;
        font-weight: lighter;
        line-height: 4rem;
        width: 65%;   
        word-spacing: .4rem; 

        @media(max-width: 768px) {
            width: 75%;
            font-size: 2.6rem;
        }

        @media(max-width: 600px) {
            width: 100%;
            padding: 6rem 2rem;
            font-size: 2.4rem;
            line-height: 3rem;
        }
    }

    &__highlight {
        font-family: 'voyage';
        letter-spacing: .4rem; 
        font-weight: 200;
        //text-decoration: underline;  
    }

    &__img {
        display: none;

        @media(max-width: 600px) {
            display: block;
            width: 90%;
            margin: auto;
            height: auto;
            border-radius: .5rem; 
        }
    }
}