.cursor {
    width: 3rem;
    height: 3rem;
    border: 2px solid var(--white);
    border-radius: 100px;
    position: absolute;
    transform: translate(-50%, -50%);
    pointer-events: none;
    z-index: 60;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    transition: width .5s, height .5s;
}

// Heading
.headingC {
    width: 10rem;
    height: 10rem;
    background-color: var(--white);
    mix-blend-mode: difference;

}

//Menu
.menuC {
    background-color: var(--white);
    mix-blend-mode: difference;
}

//About
.aboutC {
    background-image: linear-gradient(rgba(0,0,0,.5), rgba(0,0,0,.5)), url('./framework/images/saura.jpg'); 
    background-size: 100% auto;     
    width: 22%; 
    height: 60vh;    
    z-index: -1; 
    border-radius: .5rem; 
    border: none;
    //transition: transform 0.25s; 
    //will-change: transition; 
} 

// Project item
.projectC {
    background-color: var(--white);
    color: var(--black);
    font-family: 'Barlow Condensed';
    font-weight: 400;
    width: 6rem;
    height: 6rem;
    transform: translate(0%, 0%);
}
