.contact {
    background-color: var(--black);
    //border-bottom: 1px solid var(--white-light);
    padding: 10rem 8rem 8rem;
    font-family: 'suisse';
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
 
    @media(max-width: 1024px) {
        padding: 4rem;
    }

    &__item {
        min-width: 50%;

        @media(max-width: 768px) {
            width: 100%;
            margin: 3rem 0rem;
        }

        &--1 {
            background-color: black;

            h1 {
                font-family: 'Formula Condensed';
                font-size: 15rem;
                letter-spacing: .3rem;
                line-height: 16rem;

                span {
                    display: inline-block;
                    //background-image: radial-gradient( circle 862px at 6% 18%,  rgba(21,219,149,1) 9.4%, rgba(26,35,160,1) 83.6% );
                    //background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,37,174,1) 0%, rgba(241,147,55,1) 53.4%, rgba(250,237,56,1) 99.5% );
                    //background-image: linear-gradient( 143.3deg,  rgba(216,27,96,1) 33.1%, rgba(237,107,154,1) 74.9% );
                    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,37,174,1) 0%, rgba(241,147,55,1) 53.4%, rgba(250,237,56,1) 99.5% );
                    background-clip: none;
                    -webkit-text-fill-color: transparent;
                    -webkit-background-clip: text; 
                }
            }

            p {
                font-size: 1.7rem;
                line-height: 2.4rem;
                padding-bottom: 15rem;

                @media(max-width: 768px) {
                    padding-bottom: 10rem;
                }

                @media(max-width: 600px) {
                    padding-bottom: 5rem;
                }
            }
        }

        &--2 {
            background-color: black;
        }

        &--add {
            min-width: 100%;
            padding: 2rem 0rem;
        }
    }
}

// Address portion
.address {
    font-family: 'suisse'; 
    display: flex;
    justify-content: space-between;

    h5 {
        color: var(--white-light);
        font-size: 1.6rem;
        padding: 2rem 0rem;
        letter-spacing: .3rem;
        font-weight: lighter;
    }

    &__item {
        padding: 2rem;

        &--1 {
            background-color: black;
        }

        &--2 {
            background-color: black;
        }
    }

    &__links {
        display: block; 
        color: var(--white-grey);
        font-size: 4.5rem;
        padding: .5rem;

        @media(max-width: 600px) {
            font-size: 3.5rem;
        }

        @media(max-width: 600px) {
            font-size: 3rem;
        }
    }

    &__para {
        font-size: 2.5rem;
        padding: .5rem 0rem;
        color: var(--white-grey);

        @media(max-width: 600px) {
            font-size: 1.8rem;
        }

        @media(max-width: 400px) {
            font-size: 1.6rem; 
        }
    }
}

//Form Portion
.form {
    width: 100%; 
    color: var(--white-light);
    //background-color: red;
    display: grid;
    grid-template-columns: 1fr 1fr;

    @media(max-width: 600px) {
        grid-template-columns: 1fr;
    }

    &__small {
        display: inline-block;

        @media(max-width: 600px) {
            padding: 2rem 0rem; 
        }

        input {
            width: 95%;
        }
    }

    &__large {
        width: 100%;
        grid-column: 1/-1;
        padding: 2rem 0rem;
    }

    &__label {
        font-size: 1.8rem;
        letter-spacing: .2rem; 
        font-weight: 500;
    }

    &__input {
        width: 100%;
        outline: none;
        border: none;
        color: var(--white);
        padding: .5rem .5rem .8rem;
        margin: 1rem 0rem;
        background-color: transparent;
        border-bottom: 1px solid var(--white-light); 
        font-size: 1.8rem;
    }

    &__textarea {
        height: 12rem;
        width: 97.5%;
        font-family: 'suisse';
    }

    &__submit {
        background-color: transparent;
        font-family: 'suisse';
        margin: 3rem 0rem;
        padding: .4rem 0rem; 
        text-align: left; 
        border: none;
        outline: none;
        color: transparent; 
        font-size: 2.5rem; 
        color: var(--white);
        letter-spacing: .2rem;
        transition: .2s;

        &:hover {
            background-image: linear-gradient( 110.1deg,  rgba(34,126,34,1) 2.9%, rgba(168,251,60,1) 90.3% );
            background-clip: none; 
            -webkit-background-clip: text;
            color: transparent;
            font-weight: 700;
            transform: translateY(.2rem);
        }
    }
}

::placeholder {
    color: var(--white-light);
}

