.header {
    min-height: 100vh;

    h1 { 
        font-size: 13rem;
        font-weight: lighter;
        font-family: 'voyage';
        line-height: 11.8rem;
        border-radius: 0 18rem 0 0;
        text-align: center;
        position: relative;
        top: 40vh; 
        left: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;

        @media(max-width: 768px) {
            font-size: 11rem;
        }

        @media(max-width: 500px) {
            font-size: 10rem;
        }

        @media(max-width: 424px) {
            font-size: 8rem;
        }

        @media(max-width: 320px) {
            font-size: 7rem;
        }
    }
}
