.footer {
    padding: 0rem 10rem;
    font-family: 'suisse';

    @media(max-width: 768px) {
        padding: 0rem 4rem;
    }

    &__2020 {
        background-image: linear-gradient( 122.3deg,  rgba(111,71,133,1) 14.6%, rgba(232,129,166,1) 29.6%, rgba(237,237,183,1) 42.1%, rgba(244,166,215,1) 56.7%, rgba(154,219,232,1) 68.7%, rgba(238,226,159,1) 84.8% );
        //background-image: linear-gradient( 99.6deg,  rgba(112,128,152,1) 10.6%, rgba(242,227,234,1) 32.9%, rgba(234,202,213,1) 52.7%, rgba(220,227,239,1) 72.8%, rgba(185,205,227,1) 81.1%, rgba(154,180,212,1) 102.4% );
        //background-image: radial-gradient( circle 1224px at 10.6% 8.8%,  rgba(255,255,255,1) 0%, rgba(153,202,251,1) 100.2% );
        font-size: 33vw;
        text-align: center;
        background-clip: none;
        color: transparent;
        padding: 0rem; 
        -webkit-background-clip: text;

        @media(max-width: 768px) {
            font-size: 34vw;
        }
    }
    
    &__span {
        font-family: 'voyage';
        font-size: 3rem;
        position: relative;
        top: 3rem;
        left: 3rem;
    }

    &__credit {
        color: var(--white);
        display: flex;
        justify-content: space-between;
        font-family: 'Barlow Condensed';
        font-weight: 200;
        font-size: 1.8rem;
        position: relative;
        top: -5rem;

        @media(max-width: 768px) {
            top: -2rem;
            font-size: 1.5rem;
        }
    }

    &__privacy {
        color: var(--white-light);
        display: flex;
        justify-content: space-between;
        font-size: 1.6rem;
        padding-bottom: 3rem;

        a {
            color: var(--white-light);
        }
    }
}