.nav {  
    background-color: black; 
    padding: .5rem 0rem;
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    z-index: 50; 

    display: flex;
    align-items: center;
    justify-content: space-between;
    
    ////////////////////////////////////////////
    // 1=> Logo Containter
    &__logo {
        font-family: 'voyage';
        font-size: 3.8rem;
        padding: 0rem;
        margin-left: 6rem;
        transform: translateY(-1rem);
    }

    /////////////////////////////////////////////////
    // MENU => Menu btn
    &__btn {
        color: inherit; 
        text-decoration: none;
        font-family: 'Barlow Condensed';
        border: none;
        font-size: 1.9rem;
        margin: 0rem 5rem; 
        cursor: pointer;
        z-index: 50;
        border-bottom: none; 
        transition: all .2s;

        @media(max-width: 1064px) {
            font-size: 2.2rem;
        }
        
        &:hover {  
            //border-bottom: 1px solid var(--white);
            //color: black;
            font-weight: 300;      
        }
    }
    // Menu Container
    &__link {
        background-color: var(--black);  
        position: absolute;
        top: 0;
        left: 0;
        width: 0%;
        height: 100vh; 
        overflow: hidden;
        z-index: 45;
        transition: .5s;
        text-align: center;
        padding-top: 20vh;
        

        &--1{  
            font-size: 9rem;
    
            &:hover {
                background: -webkit-linear-gradient(#00c3ff, #ffff1c);
            }
        }

        &--2{   
            font-size: 9rem;
    
            &:hover {
                background: -webkit-linear-gradient(  #009fff, #ec2f4b); 
                //-webkit-background-clip: text;
            }
        }

        &--3{  
            font-size: 9rem;
    
            &:hover {
                background: -webkit-linear-gradient(  #f7ff00, #db36a4); 
                //-webkit-background-clip: text;
            }
        }
    }
}