// CSS Variables
:root {
    --black: #000000;
    --white: #ffffff;
    --white-grey: #bfbfbf;
    --white-light: #767676; 
}

// Universal Selector
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}    

// Default overwrite
html {
    font-size: 62.5%; // 10(want) / 16px = 0.625 * 100 = 62.5%
    // 1rem = 10px !IMPORTANT
    scroll-behavior: smooth;

    @media(max-width: 1064px) {
        font-size: 60%;
    }

    @media(max-width: 900px) {
        font-size: 55%;
    }

    @media(max-width: 768px) {
        font-size: 50%;
    }

    @media(max-width: 600px) {
        font-size: 43.5%;
    }
}

body {
    background-color: var(--black);
    box-sizing: border-box;
    font-size: 1.6rem;
    font-weight: 400;
    color: var(--white);
    cursor: none;

    overflow-x: hidden;
    max-width: 100%;
}

.container {
    max-width: 100%;
}

// Selection
::selection {
    //background-color: var(--white);
    //color: var(--black);
    user-select: none;
}